import React, { Component } from 'react';
import { Form, Select, Icon, Input, Button, Row, Col, Cascader, InputNumber } from 'antd';
import styles from './styles.scss';
import { connect } from 'dva';
const FormItem = Form.Item;
const Option = Select.Option;

class editSectionForm extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
    const { dispatch, order } = this.props;
    const values = this.props.form.getFieldsValue();
    let { section } = this.props;
    section.value = Object.assign([], section.value);
    if (section.value.order) {
      section.value.push(Object.assign({}, values, { order: order || section.value.length }));
    } else {
      section.value.push(Object.assign({}, values, { order: section.value.length }));
    }
    console.info('submit_section',section);

    dispatch({
      type: 'sections/patch',
      payload: { section },
    });
  }

  render() {
    const { section, order } = this.props;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 14 },
      },
    };
    const submitLayout = {
      wrapperCol: { span: 14, offset: 6 },
    };
    // console.info('section##', section, order);
    const fields = section && section.fields;
    let item;
    if (order && section && section.value && section.value[order]) {
      item = section.value[order];
    }
    const fieldItems = fields && fields.map((field, idx) => {
      return (
        <FormItem
          {...formItemLayout}
          label={field.caption}
          key={idx}
        >
          {getFieldDecorator(`${field.name}`, {
            rules: [{ required: true, message: `请输入${field.caption}` }],
            initialValue: item && item[field.name] || ''
          })(<Input name={field.name} />)}
        </FormItem>
      );
    });

    return (
      <Form onSubmit={this.handleSubmit}>
        {fieldItems}
        <FormItem {...submitLayout}>
          <Button className={styles.submit} type="primary" htmlType="submit">保存</Button>
        </FormItem>
      </Form>
    );
  }
}

function mapStateToProps(state) {
  const { list, total, page } = state.sections;
  return {
    loading: state.loading.models.sections,
    list,
    total,
    page,
  };
}

export default connect(mapStateToProps)(editSectionForm);



